import { css } from '@emotion/react';
import { useRouter } from 'next/router';
import { useState } from 'react';

import { Typography } from 'src/components/dataDisplay';
import { FullHeightBox, Stack, ThinRectangleBox } from 'src/components/layout';
import { Paper } from 'src/components/surface';
import useProductInfo from 'src/hooks/useProductInfo';
import useSurveysForBestFit from 'src/hooks/useSurveysForBestFit';

import { BASE_PATH, ROOT_PATH } from '../../../constants/resouce';
import BestFitProductQuestion from './BestFitProductQuestion';
import ProgressBar from './ProgressBar';

const contentLayout = css`
  padding: 20px;
  color: #ffffff;
`;

const stepLayout = css``;

const scrollSnapTypeMandatory = css`
  overflow: hidden;
  scroll-snap-type: x mandatory;
`;

const questionLayout = css`
  width: 100%;
`;

const nowrap = css`
  white-space: nowrap;
`;

const removeFirstSlash = (path: string) => {
  if (!path || !path.length) {
    return path;
  }
  const first = path.substring(0, 1);
  if (first !== '/') {
    return path;
  }
  return path.substring(1);
};

const BestFitProductSurvey = (): JSX.Element => {
  const router = useRouter();
  const { pathname = '', basePath = '' } = router;
  const [step, setStep] = useState(0);
  const [answers, setAnswers] = useState<number[]>([]);
  const { name: brandName } = useProductInfo();
  const { surveyBackgroundImage, surveys } = useSurveysForBestFit({
    brandName,
  });
  const progressPercent =
    surveys.length === 0 ? 0 : ((step + 1) / surveys.length) * 100;

  console.log('BestFitProductSurvey', 'step', step);
  console.log('BestFitProductSurvey', 'answers', answers);
  console.log('BestFitProductSurvey', 'surveys', surveys.length);
  console.log('BestFitProductSurvey', 'progressPercent', progressPercent);

  console.log('basePath', basePath);
  console.log('pathname', pathname);
  console.log('parentPath', pathname.split('/').slice(0, -1).join('/'));
  const handleLeftClick = () => {
    console.log('handleLeftClick', 'from', step);
    setAnswers((prev) => [...prev, 0]);
    if (step < surveys.length - 1) {
      setStep((prev) => prev + 1);
    } else {
      console.log('go result!');
      const parentPath = pathname.split('/').slice(0, -1).join('/');
      window.location.href = `${ROOT_PATH}${BASE_PATH}${basePath}${removeFirstSlash(
        parentPath,
      )}/result?id=1`;
    }
  };
  const handleRightClick = () => {
    console.log('handleRightClick', 'from', step);
    setAnswers((prev) => [...prev, 1]);
    if (step < surveys.length - 1) {
      setStep((prev) => prev + 1);
    } else {
      console.log('go result!');
      const parentPath = pathname.split('/').slice(0, -1).join('/');
      window.location.href = `${ROOT_PATH}${BASE_PATH}${basePath}${removeFirstSlash(
        parentPath,
      )}/result?id=1`;
    }
  };

  return (
    <ThinRectangleBox>
      <Paper backgroundUrl={surveyBackgroundImage}>
        <FullHeightBox>
          <Stack
            justifyContent="flex-start"
            spacing={194}
            fullWidth
            css={contentLayout}
          >
            <Stack
              direction="row"
              spacing={8}
              justifyContent="space-between"
              css={stepLayout}
            >
              <ProgressBar percent={progressPercent} />
              <Typography variant="badge" css={nowrap}>
                {step + 1} / {surveys.length}
              </Typography>
            </Stack>
            <div css={scrollSnapTypeMandatory}>
              <div css={questionLayout}>
                <BestFitProductQuestion
                  index={step}
                  onLeftClick={handleLeftClick}
                  onRightClick={handleRightClick}
                />
              </div>
            </div>
          </Stack>
        </FullHeightBox>
      </Paper>
    </ThinRectangleBox>
  );
};

export default BestFitProductSurvey;
